.display_image {
  background-size: cover;
  // object-fit: cover;
  background-image: url(/images/expect-2.jpg);
  width: 60vw;
  height: 100vh;
  background-color: green;
  overflow-y: hidden;
}
.login_side {
  img {
    width: 90px;
  }
}
#signup_button {
  background-color: #00c159;
  color: white;
  margin-top: 40px;
}
#signup_button:hover {
  background-color: #2474cf;
}
.already_signup {
  text-align: center;
}
.links {
  color: #00c159;
  text-decoration: none;
}
@media (max-width: 600px) {
  .display_image {
    display: none;
  }
}
